.Root {
    position: absolute;
    width: calc(100% - 135px);
}

.Name {
    position: relative;
    float: left;

    height: 96px;
    padding-left: 62px;
    color: #9f9f9f;
    background-color: transparent;
    border: none;
    outline: none;
    transition: color 0.3s ease;

    font-family: "Roboto", sans-serif;
    font-size: 40px;
}

.Name:hover {
    color: white;
}

.Checked {
    color: white;
    padding-left: 58px;
    border-left: 4px solid white;
}

.IconButton {
    position: relative;
    float: right;

    width: 96px;
    height: 96px;
    margin-left: -18px;
    background-color: transparent;
    border: none;
    outline: none;
}