.Root {
  position: relative;
  float: left;
  display: flex;

  height: 60px;
  background-color: #2b2b2b;
  border-radius: 30px;
  border: none;
  outline: none;
  margin-right: 24px;
  align-items: center;

  transition: opacity 0.3s ease;
  opacity: 1;
}

.Disabled {
  opacity: 0.6;
}

.Marker {
  position: relative;
  float: left;

  display: none;
  margin-left: -5px;
  margin-right: -23px;
  transition: all 0.2s ease;
}

.checkedMarker {
  display: inline-block;
}

.Text {
  position: relative;
  float: left;

  margin-right: 17px;
  margin-left: 23px;

  font-family: 'Roboto', serif;
  font-size: 28px;
  color: #9f9f9f;
}
