.Root {
    margin-bottom: 27px;
}

.Root > label {
    transition: color 0.3s ease;
}

.Invalid {
    border: 2px solid #ff5349 !important;
    caret-color: #ff5349 !important;
    color: #ff5349 !important;
}

.InvalidLabel {
    color: #ff5349 !important;
}

.Text {
    color: #9F9F9F;

    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 32px;
}