.Root {
  position: absolute;
  top: 0;
  left: 0;

  width: 4500px;
  height: 3000px;

  opacity: 0; /* For open animation */
  visibility: hidden;
  transform: translateY(-20px);
  transition: opacity 0.25s ease, transform 0.25s ease, visibility 0.25s;

  z-index: 10;
}

.Blocker {
  position: absolute;

  width: 100%;
  height: 100%;

  background-color: var(--bg);
  opacity: 0.8;
}

.Opened {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.Content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.25s ease, transform 0.25s ease, visibility 0.25s;

  border-radius: 5px;
  background-color: var(--bg);
  box-shadow: 6px 6px 10px 0 rgba(0, 0, 0, 0.66);
  opacity: 0;
}

.WideContent {
  width: 1124px;
  height: 862px;
  opacity: 1;
}

.SmallContent {
  width: 660px;
  height: 559px;
  opacity: 1;
}

.Title {
  position: absolute;
  top: 29px;
  left: 57px;

  color: #9f9f9f;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 40px;
}

.Buttons {
  position: absolute;
  bottom: 55px;
  right: 4px;

  width: 100%;
}

.WhiteButton {
  position: relative;
  float: right;
  margin-right: 53px;

  color: #9f9f9f;
  background: #212121;
  border: none;
  outline: none;
  transition: color 0.3s ease;
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
}

.WhiteButton:hover {
  color: white;
}

.RedButton {
  position: relative;
  float: right;
  margin-right: 48px;

  color: #9f9f9f;
  background: #212121;
  border: none;
  outline: none;
  transition: color 0.3s ease;
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
}

.RedButton:hover {
  color: #ff4140;
}

.Divider {
  position: absolute;
  top: 501px;
  left: 60px;

  width: calc(81.8%);
}

.Divider hr {
  height: 2px !important;
  background-color: rgba(0, 0, 0, 0.44) !important;
}

.SpecsContainer {
  position: absolute;
  top: 320px;
  left: 90px;
  width: 100%;
}

.SpecsContainer > div {
  position: relative;
  float: left;

  margin-right: 100px;
}

.RadioSpecsContainer {
  position: absolute;
  top: 157px;
  left: 320px;
  width: 100%;
}

.RadioSpecsContainer > div {
  position: relative;
  float: top;

  margin-bottom: 31px;
}

.RegimesContainer {
  position: absolute;
  top: 190px;
  left: 60px;
  width: 100%;
}

.RadioContainer {
  position: absolute;
  top: 173px;
  left: 58px;
  width: 100%;
}

.RadioLabel {
  margin-left: 20px !important;
  margin-bottom: 12px !important;
  margin-top: 12px !important;

  font-family: Roboto, sans-serif;
  font-size: 32px !important;
  font-weight: 400 !important;
  transition: color 250ms ease;
  color: #9F9F9F !important;
}

.RadioLabelActive {
  color: var(--white) !important;
}

.RadioLabel:hover {
  color: var(--white) !important;
}