.Root {
    position: absolute;
    display: inline-block;

    width: 337px;
    height: 100%;
    margin-right: 26px;
    background-color: var(--bg);
}

.Root > span {
    position: absolute;
}

.Name {
    position: absolute;
    top: -10px;
    left: -1px;

    color: #9f9f9f;
    font-family: 'Roboto Thin', sans-serif;
    font-size: 40px;
}

.OptVal {
    top: 69px;

    color: white;
    font-family: Roboto, sans-serif;
    font-size: 36px;
}

.RealVal {
    top: 119px;

    color: #9f9f9f;
    font-family: Roboto, sans-serif;
    font-size: 36px;
}

.Hidden {
    color: #212121 !important;
}

.OptMarker {
    margin-right: 40px;
}

.RealMarker {
    margin-right: 23px;
}

.units {
  font-size: 32px;
}