.Root {
    position: absolute;
    top: 200px;
    right: 55px;
    z-index: 2;
}

.ModeBarButton {
    position: relative;
    float: left;

    margin-left: 36px;
    border: 0;
    fill: white;
    outline: none;
    background-color: transparent;
    transition: all 0.3s ease;
}

.alternative {
    /*fill: #FF5349;*/
    fill: transparent;
}

.disabled {
    fill: #9f9f9f;
}